"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { PayPalScriptProvider, PayPalButtons } from "@paypal/react-paypal-js"

export default function Home() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    amount: "",
  })
  const [isLoading, setIsLoading] = useState(false)

  const createOrder = async (data: any, actions: any) => {
    return actions.order.create({
      purchase_units: [
        {
          amount: {
            value: formData.amount,
            currency_code: "USD"
          },
          description: "Donación",
        },
      ],
      application_context: {
        shipping_preference: "NO_SHIPPING"
      }
    })
  }

  const onApprove = async (data: any, actions: any) => {
    setIsLoading(true)
    try {
      const response = await fetch("/api/paypal", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          orderID: data.orderID,
        }),
      })

      const responseData = await response.json()

      if (!response.ok) {
        throw new Error(responseData.error || "Error al procesar el pago")
      }

      window.location.href = "/success"
    } catch (error) {
      console.error("Error:", error)
      alert("Hubo un error al procesar tu donación. Por favor intenta nuevamente.")
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Hero Section */}
      <section className="py-20 bg-black text-white text-center">
        <div className="container mx-auto px-4">
          <h1 className="text-4xl md:text-6xl font-bold mb-6">
            Haz la Diferencia Hoy
          </h1>
          <p className="text-xl md:text-2xl mb-8 max-w-2xl mx-auto">
            Tu donación puede cambiar vidas. Cada contribución cuenta para crear un impacto positivo en nuestra comunidad.
          </p>
        </div>
      </section>

      {/* Donation Form Section */}
      <section className="py-16 px-4">
        <Card className="max-w-md mx-auto p-6 shadow-lg border-2 border-black/10">
          <div className="space-y-6">
            <div className="space-y-2">
              <Label htmlFor="amount">Monto a Donar ($)</Label>
              <input
                id="amount"
                type="number"
                placeholder="100"
                required
                min="1"
                value={formData.amount}
                onChange={(e) => setFormData({ ...formData, amount: e.target.value })}
                className="w-full px-3 py-2 border-2 border-black/10 rounded-md focus:border-black"
              />
            </div>

            {formData.amount && Number(formData.amount) > 0 && (
              <PayPalScriptProvider options={{
                clientId: process.env.NEXT_PUBLIC_PAYPAL_CLIENT_ID || "",
                currency: "USD",
                intent: "capture",
              }}>
                <PayPalButtons
                  style={{ layout: "vertical" }}
                  createOrder={createOrder}
                  onApprove={onApprove}
                  disabled={isLoading}
                />
              </PayPalScriptProvider>
            )}
          </div>

          <div className="mt-6 text-center text-sm text-gray-500">
            <p>Pago seguro a través de</p>
            <div className="mt-2">
              <span className="font-semibold text-black">PayPal</span>
            </div>
          </div>
        </Card>
      </section>

      {/* Footer */}
      <footer className="py-8 bg-black text-white text-center">
        <div className="container mx-auto px-4">
          <p className="text-sm">
            © 2024 Plataforma de Donaciones. Todos los derechos reservados.
          </p>
        </div>
      </footer>
    </div>
  )
}
